import Vue from 'vue'
import App from './App.vue'
import VWave from 'v-wave'
Vue.prototype.$bus = new Vue()

Vue.use(VWave, {
  color: '#7e7e7e',
  initialOpacity: 0.2,
  finalOpacity: 0.3,
  duration: 0.15,
  dissolveDuration: 0.3,
  easing: 'ease-in'
})

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')

