<template>

  <div class="all max">

    <div id="container">

      <!-- 顶部导航栏 -->
      <div class="top">
        <!-- 侧滑栏按钮 -->
        <div class="sider-button">
          <button v-wave class="circle-button" @click="handleMenuClick">
            <img src="../img/siderButton.svg" alt="侧滑栏按钮" class="icon">
          </button>
        </div>
        <!-- 元辅音标识 -->
        <div class="yuan-fu">
          <div v-wave :class="['circle-button-yuan-fu', 'yuan-text', yuanState ]" @click="YuanClick">
            <p>元</p></div>
          <div v-wave :class="['circle-button-yuan-fu', fuState ]" @click="FuClick">
            <p>辅</p></div>
        </div>
      </div>

      <div class="main">

        <div id="slider" class="max">
          <!-- 配置slider组件 -->
          <slider class="max" ref="slider" :options="options" @slide='slide' @tap='onTap' @init='onInit'>
            <!-- 元音页面 -->
            <slideritem key="yuanyin">
              <yuan-yin-page class="max"></yuan-yin-page>
            </slideritem>
            <!-- 辅音页面 -->
            <slideritem key="fuyin">
              <fu-yin-page class="max"></fu-yin-page>
            </slideritem>
          </slider>
        </div>
      </div>

    </div>

    <!-- <transition name="slide-fade"> -->
    <default-detail id="detail" v-show="newPageVisible" class="max" @update:newPageVisible="newPageVisible = false"></default-detail>

  </div>
</template>

<script>

import { slider, slideritem } from 'vue-concise-slider';
import YuanYinPage from "../components/YuanYinPage";
import FuYinPage from "../components/FuYinPage";
import DefaultDetail from "./defaultDetail";
export default {
  name: 'defaultMain',
  components: {
    slider,
    slideritem,
    YuanYinPage,
    FuYinPage,
    DefaultDetail,
  },
  data () {
    return {
      active: true, //元辅当前页
      someList:[],
      options: {
        currentPage: 0, //默认显示页
        pagination: false, //显示页码
        preventRebound: true, //禁止回弹
        centeredSlides: false, //居中显示
        thresholdDistance:'2' //触发的滑动距离
      },
      newPageVisible: false, //详情页显示控制
    }
  },
  computed: {  
    yuanState: function () {
      return this.active ? 'active' : 'not-active'
    },
    fuState: function () {
      return this.active ? 'not-active' : 'active'
    }
  },

  

  methods: {  
    handleMenuClick() {
      console.log('clickMenu')
      // this.$router.push('/abc');
    },

    handleLongPressEvent(item) {
      // 处理长按事件
      console.log('打开新页面' + item)
      // this.newPageVisible = true;
    },

    YuanClick() {
      this.active = true
      this.slideTo(0)
    },
    FuClick() {
      this.active = false
      this.slideTo(1)
    },

    slide (data) {
      if(data.currentPage === 0){
        this.active = true
      }else{
        this.active = false
      }
      console.log(data)
    },
    onTap (data) {
      console.log(data)
    },
    onInit (data) {
      console.log(data)
    },
    slideTo (num) {
    this.$refs.slider.$emit('slideTo', num)
  }
  },

  created() {
    console.log('created')
    // this.$bus.$on('long-press-event', this.handleLongPressEvent);
    // this.$bus.$on('long-press-event', this.handleLongPressEvent.bind(this));

    // this.eventBusHandler = this.handleLongPressEvent.bind(this);
    // this.$bus.$on('long-press-event', this.eventBusHandler);

    this.$bus.$on('long-press-event', (item) => {
      console.log('接收到事件：', item);
      this.handleLongPressEvent(item)

    // 处理事件
    })
  },
  beforeDestroy() {
    console.log('beforeDestroy')
    this.$bus.$off('long-press-event', this.handleLongPressEvent);
  },

}

</script>

<style scoped>
.all{
  position: relative;
}


#container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: #f0f0f0;
  margin: 0;
  position: absolute;
  z-index: 1;
}
#detail {
  position: absolute;
  z-index: 2;
}

.main {  
  background-color: #171a1f;
  width: 100%;
  height: calc(100% - 60px);
  margin: 0;
  padding: 0;
}


.top {
  background-color: black;
  width: 100%;
  height: 60px;
  display: flex;
}
.sider-button{
  width: 60px;  
  height: 60px;  
  border-radius: 50%;  
}

.circle-button {  
  width: 60px;  
  height: 60px;  
  border-radius: 50%;  
  background-color: black;  
  color: white;  
  display: flex;  
  justify-content: center;  
  align-items: center;  
  border: none;  
  cursor: pointer;  
  font-size: 24px;  
  outline: none;
}  
.icon {  
  width: 20px;
  height: 20px; 
}
.max{
  width: 100%;
  height: 100%;
  margin: 0;
}
.yuan-fu{
  height: 100%;
  width: calc(100% - 120px - 80px);
  align-content: center;
  display: flex;
  justify-content: center;
  margin: 0px 40px;
}
.yuan-text{
  margin-right: 0px;
}
.active{
  color: black;
  background-color: white;
}
.not-active{
  color: white;
  background-color: black;
}
.circle-button-yuan-fu{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  align-content: center;
  display: flex;
  justify-content: center;
  margin: auto;
}
p{
  text-align: center;
  line-height: 100%;
  margin: auto;
}

.slide-fade-enter-active, .slide-fade-leave-active {
  transition: all 0.5s ease;
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateX(100%);
  opacity: 0;
}
</style>