<template>
  <div class="card container shadow"  v-wave>
    <!-- 卡片标题 -->
    <div class="card-title">
      <p class="card-title-text">{{cardTitle}}</p>
      <p class="line"></p>
    </div>

    <div class="card-grid">
      <!-- 卡片单元 -->
      <div  
        v-for="(item, index) in formattedItems"  
        :key="index" class="card-wrapper"  
      >
        <div 
          v-wave
          v-if="item !== ''" 
          class="card card-div shadow" 
          @click.prevent="playAudio(item.replace(/:/g, '-') + '.mp3')"
          @mousedown="startLongPress(item)" 
          @mouseup="stopLongPress" 
          @mouseleave="stopLongPress"
          @touchstart="startLongPress(item)"
          @touchend="stopLongPress"
          @touchcancel="stopLongPress"
          @touchleave="stopLongPress"
          >
          <p class="card-text"> {{ item }} </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'CardContainer',
  data () {
    return {
      longPressTimer: null,
    }
  },
  props: {  
    items: {  
      type: Array,  
      required: true,  
    },
    cardTitle: {  
      type: String,  
      required: true,
    },
  },  
  computed: {  
    formattedItems() {  
      // 确保数组长度是4的倍数，空位用空字符串占位  
      const length = Math.ceil(this.items.length / 4) * 4;  
      return this.items.concat(Array(length - this.items.length).fill(''));
    },
  }, 
  methods: {

    startLongPress(item) {
      this.longPressTimer = setTimeout(() => {
        this.$bus.$emit('long-press-event', item); // 触发公共自定义事件
        console.log('长按触发');
      }, 1000); // 1秒
    },
    stopLongPress() {
      clearTimeout(this.longPressTimer);
    },





    playAudio(fileName) {
      // 定义音频文件的基础路径
      const basePath = process.env.BASE_URL + 'audio/DJ/EnglishRabbit/';
      const audioUrl = `${basePath}${fileName}`;
      // 创建一个新的 Audio 对象
      const audio = new Audio(audioUrl);
      // 播放音频
      audio.play()
    },
    pauseAudio() {
      if (this.audioElement) {
        this.audioElement.pause();
      }
    },
    stopAudio() {
      if (this.audioElement) {
        this.audioElement.pause();
        this.audioElement.currentTime = 0;
      }
    },
  },
  beforeDestroy() {
    // 组件销毁前停止播放音频，避免内存泄漏
    if (this.audioElement) {
      this.stopAudio();
    }
  },
}
</script>

<style scoped>
.card{
  border-radius: 25px;
}
.shadow{
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
}
.container{
  background-color: #4b4d52;
  max-width: 500px;
  min-width: 340px;
  margin: 20px auto;
}
.card-grid {
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  row-gap: 20px;
  column-gap: 10px;
}
.card-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 0 calc(25% - 10px);
  box-sizing: border-box;
}
.card-div {
  width: 80px;
  height: 60px;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  border: 0px solid #ccc;
}
p{
  font-size: 20px;
  color: #000000;
}
.card-text{
  text-align: center;
  line-height: 100%;
}
.card-title-text{
  width: 100%;
  height: 30px;
  color: #fff;
  padding-left: 20px;
  padding-top: 15px;
  margin: 0;
  margin-bottom: 10px;
  text-align: start;
}
.line{
  width: calc(100% - 40px);
  height: 1px;
  background-color: #fff;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 10px;
  margin-bottom: 5px;
}
</style>